/* This stylesheet is only used for responsive states */

/* MINIMUM WIDTH OF 1199PX */
@media only screen and (min-width: $screen-lg-min) {

}

/* MINIMUM WIDTH OF 992PX AND MAX WIDTH OF 1198PX */
@media only screen and (max-width: $screen-lg-max) {
	.lastMileGuy1{
		display: none;
	}
	.home-mast .mastContent{
		width: 100%;
		max-width:initial;
	}

	.featureSection .featureBlock{
		margin-bottom: 40px;
	}

	.pricingSection .priceBlock{
		margin-bottom: 40px;	
	}
}

/* MINIMUM WIDTH OF 768PX AND MAX WIDTH OF 991PX */
@media only screen and (max-width: $screen-md-max) {
	.lastMileGuy2{
		background: none;
	}
	.lastMileGuy2 .col-8{
		width: 100%;
		padding-right: 0px;

		.lastMileAppSupports{
			padding-right: 0px;
		}
	}

	.site-header .container .row{
		justify-content: center;
	}
	.siteNavigation{
		margin-top:40px;
	}

	.site-header .container .row .siteNavigation>div>a.btn{
		margin-left: 0px;
	}

	.home-mast .mastContent{
		padding:50px 0;
	}
}

/* MINIMUM WIDTH OF 576PX AND MAX WIDTH OF 767PX */
@media only screen and (max-width: $screen-sm-max) {
	body{
		text-align: center;
	}

	.orangeCircle{
		margin-bottom: 20px;
	}

	.footerRight{
		text-align: center;
		margin-top: 20px;
	}
}

/* MAX WIDTH OF 575PX */
@media only screen and (max-width: $screen-xsm-max) {

}
