/* This stylesheet is for any custom classes you create in your child theme. See below for examples */

*{
	font-size: inherit;
}
body{
	font-family: $baseFont;
}

img{
	max-width: 100%;
	height: auto;
}

.container {
	&.whiteBackground{
		background-color:#ffffff;
		padding:50px;
	}
}

.containerWithPadding{
	padding:100px 0;
}


.primary-color{
	color:$primary;
}

.container{
	padding: 100px 0;
	.row{
		padding:0px;
	}
}


.featureSection{
	h2{
		margin-bottom: 80px;
	}

	.featureBlock{
	    background-color: #ffffff;
	    box-shadow: 0px 0px 40px rgba(0,0,0,0.2);
	    padding: 35px;
	    text-align: center;

	    h3{
			font-weight: 900;
		}

	    img{
	    	margin-bottom: 20px;
	    }
	}
}

.pricingSection{
	

	.priceBlock{
	    background-color: #333E4A;
	    box-shadow: 0px 0px 40px rgba(0,0,0,0.2);
	    padding: 80px 40px;
	    text-align: center;

	    p{
			color: #F05A28;
			font-family: $headingFont;
			font-size: 6rem;
			margin-bottom: 0px;
			+p{
				color: #ffffff;
				font-family: $baseFont;
				font-size: 3.2rem;
				margin-bottom: 40px;
				+p{
					font-family: $headingFont;
					font-size: 6rem;
					line-height: 6rem;
					margin-bottom: 20px;
					+p{
						font-family: $baseFont;
						font-size: 2rem;
						line-height: 2rem;
					}
				}
			}
		}
	}
}

.whiteBlock{
	background-color:#ffffff;
	padding:50px;
	text-align: center;

	img{
		margin-bottom: 40px;
	}
}


.checkMark{
    position: relative;
    padding-left: 100px;
    margin-top: 40px;
    margin-left: 40px;
	&:before{
		content: "";
		background-image:url('images/icons/check-mark.png');
		background-repeat:no-repeat;
		border-radius: 80px;
		background-position:center;
		background-color:#ffffff;
		width: 80px;
		height: 80px;
		display: block;

		position: absolute;
		left: 0px;
		top: 0px;
	}
}

.lastMileGuy2{
	&.container{
		padding: 80px 0 ;
	}
	background-image:url('images/last-mile-guy2.png');
	background-repeat:no-repeat;
	background-position:right bottom;

	.col-8{
		padding-right:80px;
	}
}


.lastMileAppSupports{
    padding-right: 200px;
    h3{
    	margin-bottom: 10px;
    }
}



.orangeCircle{
    background-color: #F05A28;
    border-radius: 150px;
    display: flex;
    color: #ffffff;
    padding: 25px;
    text-align: center;
    height: 200px;
    width: 200px;
    align-items: center;
    margin:0 auto;
}