/* This stylesheet should contain only styles for the header elements including the navigation menus */


.site-header{
	background-image: url('images/header.png');
	background-repeat:no-repeat;
	background-position:bottom;
	background-color:#5c606a;

	.container{
		padding:40px 0;
		.row{
			display: flex;
			justify-content: space-between;

			.siteNavigation{
				display: flex;
				justify-content: space-between;
				align-self: center;
				> div{
					display: flex;
					nav#mainNav{
						align-self:center;
						#menu-main-menu{
							display: flex;
							list-style: none;
							padding:0px;
							justify-content: space-between;
							margin-bottom: 0px;
							li{
								a{
									color:#ffffff;
									padding:10px;
									font-size: 2rem;

									&:hover{
										text-decoration: none;
										color: #F05A28;
									}
								}
							}
						}
					}

					> a.btn{
						margin-left: 20px;
						border-radius: 0px;
						text-transform: uppercase;
					}
				}
			}

		}
	}
}



.home-mast{
	position:relative;
	h1{
		color: #fff;
		margin-bottom: 65px;
	}
	> div{
		display: flex;
	}

	.mastContent{
		padding:135px 0;
		max-width: 700px;
		p{
			color:#ffffff;
			font-size: 2.7rem;
			margin-bottom: 50px;
		}
	}

	.lastMileGuy1{
		background-image:url('images/last-mile-guy.png');
		background-repeat: no-repeat;
		background-size:cover;
		width: 342px;
		height: 869px;

	    position: absolute;
	    right: 0px;
	    top: -60px;
	}
}