/* This stylesheet should be used only for button styles on your site unless you are strictly using bootstraps .btn styles */

button, .btn {
    /* Styles for all buttons here */
    font-size:2rem;
    padding: 10px 30px;

    &:hover {
        /* Default hover styles for all buttons */
    }

    &.btn-primary {
        /* Styles for primary buttons here */
        background-color: #F05A28;
        border-color: #F05A28;
        &:hover {
            /* Hover styles for primary buttons */
            background-color: darken(#F05A28,.6);
        }

        &:active{
            background-color: darken(#F05A28,.6);
        }
    }

    &.btn-secondary {
        /* Styles for secondary buttons here */

        &:hover {
            /* Hover styles for secondary buttons */
        }
    }
}
